/*global $ navigator*/
import { Component } from 'react'
import Link from 'next/link'

export default class Header extends Component {
    componentDidMount () {
        if ('serviceWorker' in navigator) {
        // navigator.serviceWorker
        //   .register(process.env.BACKEND_URL +'/service-worker.js')
        //   .then(registration => {
        //     console.log('service worker registration successful')
        //   })
        //   .catch(err => {
        //     console.warn('service worker registration failed', err.message)
        //   })
        }
        
        $(window).scroll(function() {
            $(window).scrollTop() > 0 && $(window).width() > 550 ? $("header").addClass("resize") : $("header").removeClass("resize")
        })
        $(window).on('load', function() {
            $(window).scrollTop() > 0 && $(window).width() > 550 ? $("header").addClass("resize") : $("header").removeClass("resize")
            $('body').addClass("loaded")
        })
        
        $("header nav a").hover(function() {
            $("header navslider").css({
                left: $(this).position().left + parseInt($(this).css("marginLeft")) + "px",
                width: $(this).width() + "px",
                opacity: ".999"
            })
        }, function() {
            $("header navslider").css("opacity", "0")
        });
    }
    render () {
        return (
            <header>
                <logo><text>AVN SYSTEMS</text></logo>
                <nav>
                    <Link href='/' as={ process.env.BACKEND_URL + `/`}><a>Home</a></Link>
                    <Link href='/about' as={ process.env.BACKEND_URL + `/about`}><a>About</a></Link>
                    <Link href='/tvmaze' as={ process.env.BACKEND_URL + `/tvmaze`}><a>TV Maze</a></Link>
                    <Link href='/404' as={ process.env.BACKEND_URL + `/404`}><a>404</a></Link>
                </nav>
                <navtoggle><bar></bar><bar></bar><bar></bar></navtoggle>
                <navslider></navslider>
                
                <loadindicator>
                  <stripe></stripe>
                </loadindicator>
                
                <style jsx>{`
                    header {
                        overflow: hidden;
                        position: fixed;
                        z-index: 10000;
                        display: none;
                        height: 90px;
                        width: 100%;
                        transition: all 150ms ease-in-out;
                    }
                    header.resize {
                        box-shadow: 0 -1px 5px 5px rgba(0, 49, 96, 0.5);
                        background: #FFF;
                        height: 58px;
                    }
                    header logo {
                        position: absolute;
                        padding: 16px 0px;
                        cursor: pointer;
                        width: 170px;
                        left: 90px;
                        top: 10px;
                        
                        background-image: url(${process.env.BACKEND_URL}/static/images/icon.png);
                        background-repeat: no-repeat;
                        background-size: contain;
                        
                        -webkit-filter: grayscale(100%) brightness(2);
                        transition: all 150ms ease-in-out;
                    }
                    header.resize logo {
                        top: 1px;
                    }
                    header.resize logo,
                    header logo:hover {
                        -webkit-filter: none;
                        -moz-filter: none;
                        -o-filter: none;
                        filter: none;
                    }
                    header logo:hover text,
                    header.not(.resize) logo:hover {
                        color: white;
                    }
                    header logo text {
                        display: -webkit-inline-box;
                        text-transform: uppercase;
                        letter-spacing: 1px;
                        position: relative;
                        font-weight: bold;
                        min-width: 110px;
                        font-size: 14px;
                        color: #5f5f5f;
                        left: 60px;
                    }
                    header nav {
                        margin: 0 20px;
                        float: right;
                    }
                    header.resize nav a {
                        margin: 19px 10px;
                    }
                    header nav a {
                        text-transform: uppercase;
                        text-decoration: none;
                        display: inline-block;
                        letter-spacing: 1px;
                        font-weight: bold;
                        margin: 25px 10px;
                        font-size: 14px;
                        color: #d9d9d9;
                        -webkit-transition: margin 150ms ease-in-out;
                    }
                    header nav a:hover {
                        color: #8500FF;
                    }
                    header.resize navtoggle {
                        top: 5px;
                    }
                    header navtoggle {
                        display: inline-block;
                        position: absolute;
                        cursor: pointer;
                        width: 45px;
                        left: 20px;
                        top: 15px;
                        transition: all 150ms ease-in-out;
                    }
                    header navtoggle bar {
                        transition: all 250ms ease-in-out;
                        border-bottom: 2px solid #aaa;
                        display: block;
                        margin: 7px 0;
                        width: 100%;
                        height: 5px;
                    }
                    header navtoggle:hover bar {
                        border-bottom: 2px solid #bc73ff;
                    }
                    header.resize navslider {
                        top: 42px;
                    }
                    header navslider {
                        border-bottom: 4px solid #8500FF;
                        position: absolute;
                        height: 0;
                        top: 50px;
                        
                        -webkit-transition: all 250ms ease-in-out;
                        -moz-transition: all 250ms ease-in-out;
                        -ms-transition: all 250ms ease-in-out;
                        -o-transition: all 250ms ease-in-out;
                        transition: all 250ms ease-in-out;
                    }
                    
                    header loadindicator {
                        width: 50%;
                        overflow: visible;
                        -webkit-transform: translateX(-100%);
                        transform: translateX(-100%);
                    }
                    header loadindicator.show {
                        -webkit-animation: sb-loadingIndicator-slide 1.5s cubic-bezier(.75,0,.5,1) infinite;
                        animation: sb-loadingIndicator-slide 1.5s cubic-bezier(.75,0,.5,1) infinite;
                    }
                    header loadindicator stripe {
                        height: 4px;
                        border-radius: 4px;
                        background: #00a862;
                        box-shadow: 0 1px 5px rgba(0,168,98,.5);
                        transition: background .25s ease,box-shadow .25s ease;
                    }
                    
                    @-webkit-keyframes sb-loadingIndicator-slide {
                      from { -webkit-transform: translateX(-100%);
                                transform: translateX(-100%); }
                      to { -webkit-transform: translateX(200%);
                                transform: translateX(200%); }
                    }
                    @keyframes sb-loadingIndicator-slide {
                      from { -webkit-transform: translateX(-100%);
                                transform: translateX(-100%); }
                      to { -webkit-transform: translateX(200%);
                                transform: translateX(200%); }
                    }
                    
                    @media (max-width: 625px) {
                        header {
                            background: white;
                            height: 58px;
                        }
                        header logo {
                            left: calc(50% - 85px);
                            -webkit-filter: none;
                            -moz-filter: none;
                            -o-filter: none;
                            filter: none;
                            top: 1px;
                        }
                        header nav {
                            display: none;
                        }
                        header navtoggle {
                            top: 5px;
                        }
                    }
                `}</style>
            
                <style jsx global>{`
                    body.left-menu header navtoggle bar:first-child {
                        transform: rotate(45deg);
                        position: relative;
                        top: 15px;
                    }
                    body.left-menu header navtoggle bar:nth-child(2) {
                        transform: rotate(135deg);
                        background: #000000;
                        position: relative;
                        top: 5px;
                    }
                    body.left-menu header.resize navtoggle bar:nth-child(2) {
                        background: #f4f4f4;
                    }
                    body.left-menu header navtoggle bar:nth-child(3) {
                        opacity: 0;
                    }
                    
                    @media (min-width: 1250px) {
                        body.left-menu header {
                            width: calc(100% - 330px);
                            margin-left: 330px;
                        }
                    }
                    
                    @media (max-width: 1250px) {
                        body.left-menu header logo {
                            left: 400px!important;
                        }
                        body.left-menu header navtoggle {
                            left: 345px!important;
                        }
                    }
                    
                    @media (max-width: 880px) {
                        body.left-menu header nav {
                            display: none;
                        }
                    }
                    
                    @media (max-width: 625px) {
                        body.left-menu header navtoggle bar:nth-child(2) {
                            background: #f4f4f4;
                        }
                    }
                    
                    @media (max-width: 399px) {
                        body.left-menu header navtoggle {
                            left: calc(100% - 55px)!important;
                        }
                    }
                    
                    @media (max-width: 329px) {
                        body.left-menu header navtoggle {
                            left: 271px !important;
                        }
                    }
                    
                `}</style>
            </header>
        )
    }
}